import React, { useState } from "react";

export default function MyFunc() {
  const [txt, setTxt] = useState("Baramati");
  const [tmp, setTmp] = useState(20);
  const [hmd, setHmd] = useState(20);
  const [Wth, setWth] = useState("Clouds");
  const [cd, setCd] = useState("02d");
  function handelClick() {
    console.log("clicked");
    if (txt.length > 3) {
      fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=${txt}&appid=4ae2feb50731d0699e2b1d5cd6d4d5f4`
      )
        .then((response) => {
          return response.json();
        })
        .then((users) => {
          console.log(users);
          setTmp(parseInt(users.main.temp - 273.15));
          setHmd(users.main.humidity);
          setWth(users.weather[0].main);
          setCd(users.weather[0].icon);
        });
    } else {
      document.getElementById("alert").hidden = false;
      setTimeout(() => {
        document.getElementById("alert").hidden = true;
      }, 5000);
    }
  }
  return (
    <>
      <div className="main d-flex justify-content-center ">
        <div className="sub-main">
          <div hidden class="alert alert-danger" role="alert" id="alert">
            City Name Cannot Remain Empty or City is invalid
          </div>
          <input
            type="text"
            id="txt"
            value={txt}
            onChange={(e) => setTxt(e.target.value)}
          />
          
            <i id='i' class="bi bi-search" onClick={handelClick}></i>
            
    
          <div className="sub-sub-main">
            <h2 className='h2 mt-3'>
              Weather for <strong>"{txt}"</strong>
            </h2>

            <img
              className="container"
              src={`https://openweathermap.org/img/wn/${cd}@2x.png`}
              alt="..."
            />

            <table className="table table-light">
              <thead>
                <tr>
                  <th scope="col">Temp</th>
                  <th scope="col">Weather</th>
                  <th scope="col">Humidity</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{tmp}Â°C</td>
                  <td>{Wth}</td>
                  <td>{hmd}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <div className="p-1">
          <h2><u> Top Cities Weather</u></h2>
          <table className='table table-light'>
              <thead>
                  <tr>
                      <th>Cities</th>
                      <th>Temp</th>
                  </tr>
              </thead>
              <tbody>
                  <tr>
                      <td>Mumbai</td>
                      <td>23</td>
                  </tr>
                  <tr>
                      <td>Delhi</td>
                      <td>22</td>
                  </tr>
                  <tr>
                      <td>Kolkata</td>
                      <td>20</td>
                  </tr> <tr>
                      <td>Chennai</td>
                      <td>23</td>
                  </tr>
                  <tr>
                      <td>Jaipur</td>
                      <td>26</td>
                  </tr>
              </tbody>
          </table>
      </div>
    </>
  );
}
